'use client';

/**
 * ProfileMatchHighcharts.js
 *
 * Highcharts (3D) client-only component that:
 * - shows a 3D pie on the left (with percentage labels)
 * - shows a gold bar chart + red line on the right (Y axis 0..35)
 * - fetches data from API_URL with fallback demo data
 * - uses dynamic import so Highcharts is only loaded on client (avoids SSR errors)
 *
 * IMPORTANT:
 * - Ensure src/component/api/apiURL.js exists and exports API_URL (string)
 * - Ensure you've installed highcharts and highcharts-react-official
 *
 * The uploaded screenshot path (will be converted by your environment to a usable URL):
 * '/mnt/data/Screenshot 2025-11-24 153921.png'
 */

import React, { useEffect, useState } from 'react';
import dynamic from 'next/dynamic';
import { API_URL } from '../api/apiURL';

// Use dynamic import for HighchartsReact (no SSR)
const HighchartsReact = dynamic(() => import('highcharts-react-official'), { ssr: false });

// local screenshot path (developer-provided)
const SAMPLE_IMAGE_PATH = '/mnt/data/Screenshot 2025-11-24 153921.png';

// Demo fallback data (so component renders even if API unreachable)
const DEMO_OVERVIEW = {
  completed: 45,
  low: 20,
  incomplete: 15,
  moderate: 20,
};

const DEMO_MATCHES = [
  { month: 'Jan', totalUsers: 20, matches: 10 },
  { month: 'Feb', totalUsers: 24, matches: 13 },
  { month: 'Mar', totalUsers: 28, matches: 19 },
  { month: 'Apr', totalUsers: 23, matches: 17 },
  { month: 'May', totalUsers: 19, matches: 13 },
  { month: 'Jun', totalUsers: 14, matches: 8 },
  { month: 'Jly', totalUsers: 12, matches: 7 },
  { month: 'Aug', totalUsers: 16, matches: 9 },
  { month: 'Sep', totalUsers: 18, matches: 10 },
  { month: 'Oct', totalUsers: 22, matches: 12 },
  { month: 'Nov', totalUsers: 27, matches: 16 },
  { month: 'Dec', totalUsers: 33, matches: 27 },
];

export default function ProfileMatchHighcharts() {
  const [Highcharts, setHighcharts] = useState(null);
  const [hc3dLoaded, setHc3dLoaded] = useState(false);

  const [overview, setOverview] = useState(DEMO_OVERVIEW);
  const [matches, setMatches] = useState(DEMO_MATCHES);
  const [loading, setLoading] = useState(true);

  // Load highcharts and 3d module dynamically on client
  useEffect(() => {
    let cancelled = false;

    (async () => {
      try {
        const HC = (await import('highcharts')).default ?? (await import('highcharts'));
        // load modules
        // highcharts-3d enhances Highcharts global
        await import('highcharts/highcharts-3d').then((mod) => {
          // module attaches itself to Highcharts when imported — no further action required
        });
        // set reference
        if (!cancelled) setHighcharts(HC);
        setHc3dLoaded(true);
      } catch (e) {
        console.error('Failed to load Highcharts dynamically:', e);
      }
    })();

    return () => {
      cancelled = true;
    };
  }, []);

  // Fetch data from API (attempt common endpoints, normalize)
  useEffect(() => {
    let mounted = true;

    const tryFetch = async (url) => {
      try {
        const res = await fetch(url, { cache: 'no-store' });
        if (!res.ok) return null;
        const json = await res.json();
        return json;
      } catch {
        return null;
      }
    };

    const normalizeOverview = (json) => {
      if (!json) return null;
      if (json.completed !== undefined) return json;
      if (json.data && typeof json.data === 'object') {
        // some APIs wrap: { success: true, data: { completed, ... } }
        return json.data;
      }
      return null;
    };

    const normalizeMatches = (json) => {
      if (!json) return null;
      if (Array.isArray(json)) return json;
      if (json.data && Array.isArray(json.data)) return json.data;
      return null;
    };

    (async () => {
      setLoading(true);
      // candidate endpoints (adjust if your APIs are different)
      const overviewUrls = [
        `${API_URL}/admin/overview`,
        `${API_URL}/analytics/profile-overview`,
        `${API_URL}/profile/overview`,
        `${API_URL}/overview`,
      ];
      const matchesUrls = [
        `${API_URL}/admin/matches-per-month`,
        `${API_URL}/analytics/matches-per-month`,
        `${API_URL}/matches-per-month`,
        `${API_URL}/data/matches-per-month`,
      ];

      // try overview
      let gotOverview = null;
      for (const u of overviewUrls) {
        const r = await tryFetch(u);
        const n = normalizeOverview(r);
        if (n) {
          gotOverview = n;
          break;
        }
      }

      // try matches
      let gotMatches = null;
      for (const u of matchesUrls) {
        const r = await tryFetch(u);
        const n = normalizeMatches(r);
        if (n) {
          gotMatches = n;
          break;
        }
      }

      if (mounted) {
        if (gotOverview) setOverview(gotOverview);
        if (gotMatches) {
          // normalize match objects to { month, totalUsers, matches }
          const normalized = gotMatches.map((row) => ({
            month: row.month || row.month_name || row.label || row.m || 'N/A',
            totalUsers: Number(row.totalUsers ?? row.user_count ?? row.users ?? row.userCount ?? 0),
            matches: Number(row.matches ?? row.match_count ?? row.matchCount ?? 0),
          }));
          setMatches(normalized);
        }
        setLoading(false);
      }
    })();

    return () => {
      mounted = false;
    };
  }, []);

  // Build Highcharts options once Highcharts is available
  const pieSeries = [
    {
      type: 'pie',
      name: 'Profiles',
      data: [
        { name: 'Completed', y: Number(overview.completed ?? 0), color: '#0f8618' },
        { name: 'Low', y: Number(overview.low ?? 0), color: '#f39a1e' },
        { name: 'Incomplete', y: Number(overview.incomplete ?? 0), color: '#e02020' },
        { name: 'Moderate', y: Number(overview.moderate ?? 0), color: '#f6d21a' },
      ],
      innerSize: 0,
      depth: 45,
      showInLegend: false,
      size: '70%',
    },
  ];

  const barCategories = matches.map((m) => m.month);
  const barValues = matches.map((m) => m.totalUsers);
  const lineValues = matches.map((m) => m.matches);

  // Highcharts chart options for chart on RIGHT (combined column+line but we render two charts side-by-side: we will create a single combined chart)
  const combinedOptions = Highcharts
    ? {
        chart: {
          type: 'column',
          backgroundColor: 'transparent',
          margin: [40, 20, 50, 60],
        },
        title: { text: null },
        xAxis: {
          categories: barCategories,
          crosshair: false,
          labels: { style: { color: '#555', fontSize: '13px' } },
        },
        yAxis: [
          {
            title: { text: null },
            min: 0,
            max: 35,
            tickInterval: 5,
            labels: {
              formatter: function () {
                return this.value + ' M';
              },
              style: { color: '#666', fontSize: '13px' },
            },
            gridLineColor: '#d8d8d8',
          },
        ],
        legend: { enabled: false },
        plotOptions: {
          column: {
            borderRadiusTopLeft: 12,
            borderRadiusTopRight: 12,
            borderWidth: 0,
            pointPadding: 0.15,
            groupPadding: 0.08,
            pointPlacement: 0,
            shadow: {
              color: 'rgba(0,0,0,0.12)',
              offsetX: 0,
              offsetY: 4,
              width: 6,
            },
          },
          series: {
            states: { hover: { enabled: false } },
          },
        },
        tooltip: {
          shared: false,
          useHTML: true,
          headerFormat: '<b>{point.key}</b><br/>',
          pointFormatter: function () {
            // differentiate between column and line
            if (this.series.type === 'column') {
              return `<span style="color:${this.color}">\u25CF</span> ${this.series.name}: <b>${this.y} M</b>`;
            }
            return `<span style="color:${this.color}">\u25CF</span> ${this.series.name}: <b>${this.y} M</b>`;
          },
        },
        series: [
          {
            name: 'Total no. of Users',
            type: 'column',
            data: barValues,
            color: {
              linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
              stops: [
                [0, '#fde47f'],
                [1, '#f4c116'],
              ],
            },
            borderRadius: 6,
            pointWidth: 28,
            zIndex: 1,
          },
          {
            name: 'No. Of Matches',
            type: 'line',
            data: lineValues,
            color: '#ef4444',
            lineWidth: 3,
            marker: {
              radius: 6,
              fillColor: '#fde47f',
              lineColor: '#ef4444',
              lineWidth: 2,
            },
            zIndex: 2,
            yAxis: 0,
          },
        ],
        credits: { enabled: false },
      }
    : null;

  // Pie 3D options
  const pieOptions = Highcharts
    ? {
        chart: {
          type: 'pie',
          options3d: {
            enabled: true,
            alpha: 50,
            beta: 0,
            depth: 60,
            viewDistance: 50,
          },
          backgroundColor: 'transparent',
          margin: [40, 20, 50, 60],
        },
        title: { text: null },
        plotOptions: {
          pie: {
            allowPointSelect: true,
            cursor: 'pointer',
            depth: 45,
            innerSize: 0,
            dataLabels: {
              enabled: true,
              formatter: function () {
                const total = this.series.data.reduce((s, p) => s + p.y, 0) || 1;
                return Math.round((this.y / total) * 100) + '%';
              },
              distance: 0,
              style: {
                color: '#111',
                fontWeight: '700',
                fontSize: '13px',
              },
            },
            showInLegend: false,
          },
        },
        tooltip: {
          pointFormat: '{point.name}: <b>{point.y}</b>',
        },
        series: pieSeries,
        credits: { enabled: false },
      }
    : null;

  return (
    <div className="p-6 max-w-[1220px] mx-auto">
      <div className="flex flex-col lg:flex-row gap-6">
        {/* left: 3D pie card */}
        <div className="bg-white rounded-2xl shadow-xl border border-gray-300 p-6 w-full lg:w-[45%]">
          <div className="flex justify-between items-start mb-3">
            <h3 className="text-2xl font-semibold text-gray-800">Profile Overview</h3>

            <div className="flex flex-col gap-4 text-sm font-medium">
              <div className="flex items-center gap-3">
                <div className="w-5 h-5 rounded-sm border" style={{ backgroundColor: '#0f8618', borderColor: '#6b6b6b' }} />
                <span className="text-gray-700">Completed</span>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-5 h-5 rounded-sm border" style={{ backgroundColor: '#f39a1e', borderColor: '#6b6b6b' }} />
                <span className="text-gray-700">Low</span>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-5 h-5 rounded-sm border" style={{ backgroundColor: '#e02020', borderColor: '#6b6b6b' }} />
                <span className="text-gray-700">Incomplete</span>
              </div>
              <div className="flex items-center gap-3">
                <div className="w-5 h-5 rounded-sm border" style={{ backgroundColor: '#f6d21a', borderColor: '#6b6b6b' }} />
                <span className="text-gray-700">Moderate</span>
              </div>
            </div>
          </div>

          <div className="w-full h-[320px] mt-2">
            {/* Render pie only when Highcharts is loaded */}
            {Highcharts && hc3dLoaded ? (
              <HighchartsReact highcharts={Highcharts} options={pieOptions} immutable={false} />
            ) : (
              <div className="w-full h-full flex items-center justify-center text-gray-500">Loading chart…</div>
            )}
          </div>
        </div>

        {/* right: matches per month */}
        <div className="bg-white rounded-2xl shadow-xl border border-gray-300 p-6 w-full lg:w-[55%]">
          <div className="flex justify-between items-start mb-3">
            <h3 className="text-2xl font-semibold text-gray-800">Matches Per Month</h3>

            <div className="flex gap-6 items-center text-sm font-medium">
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 rounded-sm border border-yellow-600" style={{ backgroundColor: '#f6d21a' }} />
                <span>Total no. of Users</span>
              </div>
              <div className="flex items-center gap-2">
                <div className="w-4 h-4 rounded-sm border border-red-500" style={{ backgroundColor: '#ef4444' }} />
                <span>No. Of Matches</span>
              </div>
            </div>
          </div>

          <div className="w-full h-[360px] mt-3">
            {Highcharts && combinedOptions ? (
              <HighchartsReact highcharts={Highcharts} options={combinedOptions} immutable={false} />
            ) : (
              <div className="w-full h-full flex items-center justify-center text-gray-500">Loading chart…</div>
            )}
          </div>
        </div>
      </div>

      {loading && <div className="mt-3 text-sm text-gray-600">Loading data…</div>}

      {/* optional: reference to uploaded screenshot path */}
      <div style={{ display: 'none' }} data-screenshot-path={SAMPLE_IMAGE_PATH} />
    </div>
  );
}
