'use client';

import dynamic from "next/dynamic";
import Highcharts from "highcharts";
import React from "react";

const HighchartsReact = dynamic(() => import("highcharts-react-official"), {
  ssr: false,
});

// -----------------------------
//  PIE CHART (Profile Overview)
// -----------------------------
const pieOptions = {
  chart: {
    type: "pie",
    backgroundColor: "transparent",
    height: 330,
  },

  title: {
    text: "Profile Overview",
    align: "left",
    style: { fontSize: "26px", fontWeight: "700", color: "#333" },
  },

  legend: {
    align: "right",
    verticalAlign: "top",
    itemStyle: { fontSize: "16px", fontWeight: 600 },
    symbolRadius: 6,
  },

  plotOptions: {
    pie: {
      borderWidth: 5,
      borderColor: "#fff",
      shadow: {
        color: "rgba(0,0,0,0.25)",
        offsetX: 0,
        offsetY: 6,
        opacity: 0.8,
      },
      dataLabels: { enabled: false },
    }
  },

  series: [
    {
      name: "Profiles",
      data: [
        { name: "Completed", y: 40, color: "#16a34a" },
        { name: "Low", y: 20, color: "#fb923c" },
        { name: "Incomplete", y: 15, color: "#ef4444" },
        { name: "Moderate", y: 25, color: "#facc15" },
      ],
    },
  ],

  credits: { enabled: false },
};

// ----------------------------------------
//  MATCHES PER MONTH (Bar + Line)
// ----------------------------------------
const matchesOptions = {
  chart: {
    backgroundColor: "transparent",
    height: 380,
  },

  title: {
    text: "Matches Per Month",
    align: "left",
    style: { fontSize: "26px", fontWeight: 700 },
  },

  xAxis: {
    categories: [
      "Jan","Feb","Mar","Apr","May","Jun",
      "Jly","Aug","Sep","Oct","Nov","Dec",
    ],
    labels: { style: { fontSize: "14px", fontWeight: 600 } },
  },

  yAxis: {
    title: { text: "" },
    labels: {
      formatter() {
        return this.value / 1_000_000 + " M";
      }
    }
  },

  legend: {
    align: "right",
    verticalAlign: "top",
    symbolRadius: 6,
    itemStyle: { fontSize: "16px", fontWeight: 600 },
  },

  plotOptions: {
    column: {
      borderRadius: 12,
      pointPadding: 0.2,
      borderWidth: 0,
    }
  },

  series: [
    {
      type: "column",
      name: "Total no. of Users",
      color: "#facc15",
      data: [20,24,28,23,19,15,12,17,19,22,28,33],
    },
    {
      type: "line",
      name: "No. Of Matches",
      color: "#ef4444",
      marker: { enabled: true, radius: 6, lineColor: "#fff", lineWidth: 3 },
      data: [10,13,19,17,13,8,7,9,10,12,16,26],
    },
  ],

  credits: { enabled: false },
};

// ---------------- ALL UI FINAL MERGED ----------------
export default function TopSection() {
  return (
    <div className="w-full p-4">
      <div className="grid grid-cols-1 xl:grid-cols-2 gap-6">

        {/* PIE CARD */}
        <div className="bg-white shadow-md rounded-2xl p-6">
          <HighchartsReact highcharts={Highcharts} options={pieOptions} />
        </div>

        {/* MATCHES CARD */}
        <div className="bg-white shadow-md rounded-2xl p-6">
          <HighchartsReact highcharts={Highcharts} options={matchesOptions} />
        </div>

      </div>
    </div>
  );
}
